import React, { useState, useEffect } from 'react';
import styled, { css } from 'styled-components/macro';
import { Link, useLocation } from 'react-router-dom';
import { menuData } from '../data/MenuData';
import { Button } from './Button';
import { HiOutlineMenuAlt3 } from "react-icons/hi";

const Nav = styled.nav`
  height: 60px;
  display: flex;
  background: transparent;
  justify-content: space-between;
  padding: 1rem 2rem;
  z-index: 100;
  position: fixed;
  width:100%;
`;

const NavLink = css`
  color: #fff;
  font-weight: 600;
  display: flex;
  align-items: center;
  padding: 0 1rem;
  height: 100%;
  cursor: pointer;
  text-decoration: none;
  text-shadow: 0px 0px 20px rgba(0,0,0,1);

  &:hover{
    transform: translateY(-2px);
  }
`

const Logo = styled(Link)`
  ${NavLink}
  font-weight: bold;
  padding: .3rem;
  letter-spacing: 2px;
  color:#f2f2f2;
  text-shadow: 5px 5px 10px rgba(0,0,0,1);
`;


const MenuBars = styled(HiOutlineMenuAlt3)`
  display: none;
  color:#fff;
  background-color: rgb(7,46,110);
  padding:5px;
  border-radius: 20%;

  @media screen and (max-width: 768px){
    display: block;
    background-size: contain;
    height: 40px;
    width: 40px;
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-50%, 25%);
  }
`;

const NavMenu = styled.div`
  display: flex;
  align-items: center;
  margin-right: -48px;

  @media screen and (max-width: 768px){
    display: none;
  }
`;



const NavMenuLinks = styled(Link)`
  ${NavLink}
`;

const NavBtn = styled.div`
  display:flex;
  align-items: center;
  margin-right: 24px;

  @media screen and (max-width: 768px){
    display: none;
  }

`;



const Navbar = ({toggle}) => {
  const [ navbar, setNavbar ] = useState(false);
  const location = useLocation();

  const changeBackground = () => {
    if(window.pageYOffset >= 60) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };

  useEffect(() => {
    const watchScroll = () => {
      window.addEventListener('scroll', changeBackground)
    };

    watchScroll();

    return () => {
      window.removeEventListener('scroll', changeBackground);
    };
  }, []);

  let style = {
    backgroundColor: navbar || location.pathname !== "/" ? '#CD853F' : 'transparent',
    transition: '0.4s'
  }


  return (
    <Nav style={style}>
      <Logo to='/'>BOOK HEMISPHERE</Logo>
      <MenuBars onClick={toggle}/>
      <NavMenu>
        {menuData.map((item, index) => (
          <NavMenuLinks to={item.link} key={index}>{item.title}</NavMenuLinks>
        ))}
      </NavMenu>
      <NavBtn><Button to="/contact" primary='true'>Recommend a book</Button></NavBtn>

    </Nav>
  );
}

export default Navbar
